body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

/*brand font*/
/*font-family: 'Pacifico', cursive;*/
/*all other font*/
/*font-family: 'Open Sans', sans-serif;*/

.album {
  background-color: #e5e5d8;
  color: maroon;
  font-family: 'Open Sans', sans-serif;
}

table {
    border-collapse: collapse;
}

*, ::after, ::before {
    box-sizing: border-box;
}
 Row {
   display: flex;
   flex-wrap: wrap;
   padding: 1em;
   justify-content: center;
}
/*audio*/
tr.song {
    color: darkblue;
}

.audio-button {
    color: white;
    background-color: black;
    opacity: 0.7;
    margin: 4px 2px;
    border: none;
    width: 5em;
    padding: 0.2em;
    border-radius: 12px;
    text-align: center;
}

span.icon.ion-md-skip-backward {
    margin-right: 1em;
}

span.ion-md-play:before {
    content: "\F357";
    margin-right: 1em;
}

span.ion-md-skip-forward:before {
    content: "\F37D";
    margin-left: 1em;
}

input.seek-bar {
    -webkit-appearance: none;
    -moz-appearance: none;
         appearance: none;
    width: 50%;
    height: 4px;
    background: gray;
    outline: none;
    opacity: 0.7;
    -webkit-transition: .2s;
    -webkit-transition: opacity .2s;
    transition: opacity .2s;
    cursor: pointer;
}

.seek-bar::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 20px;
    height: 10px;
    border-radius: 12px;
    background: black;
    cursor: pointer;
}

.music-selection-table {
  margin-left: 43%;
}

.album-img {
    margin-top: 80px;
}

.icon.ion-md-volume-high {
    padding-bottom: 30px;
}

